@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
:root {
  --clr-paleBlue: #e0e8ff;
  --clr-brightBlue: #3829e0;
  --clr-veryPlateBlue: #f5f7ff;
  --clr-desaturatedBlue: #7280a7;
  --clr-darkBlue: #1f2f56;
  --clr-white: #fff;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  background-color: aliceblue;
  height: 100vh;
  display: grid;
  place-items: center;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

:root {
  --ff-primary: "Red Hat Display", sans-serif;
}

body {
  font-family: var(--ff-primary);
  line-height: 1.6;
  color: var(--clr-desaturatedBlue);
}

h1, h2 {
  line-height: var(--line-height);
  color: var(--clr-darkBlue);
  font-weight: 900;
  text-transform: capitalize;
}

h1 {
  font-size: 2.2rem;
  margin-bottom: 1.6rem;
}

h2 {
  font-size: 1.4rem;
  margin-bottom: 0.3rem;
}

@media screen and (min-width: 375px) {
  h1 {
    font-size: 2.7rem;
    margin-bottom: 1.7rem;
  }

  h2 {
    font-size: 1.6rem;
    margin-bottom: 0.6rem;
  }
}
.container {
  height: 90rem;
  width: 95vw;
  max-width: 140rem;
  background: url("../images/pattern-background-desktop.svg"), var(--clr-paleBlue);
  background-repeat: no-repeat;
}

.btn {
  font-size: 1.5rem;
  font-weight: 900;
  line-height: var(--line-height);
  padding: 1.5rem 0;
  background-color: var(--clr-brightBlue);
  color: var(--clr-white);
  width: 100%;
  text-align: center;
  box-shadow: var(--box-shadow-btn);
  border-radius: var(--box-shadow-btn);
}

.btn:hover {
  background-color: var(--clr-desaturatedBlue);
}

:root {
  --border-radius: 20px;
  --border-radius-btn: 11px;
  --line-height: 1.3;
  --box-shadow-btn: 0px 20px 20px rgba(56, 42, 225, 0.190291);
  --bow-shadow-card: 0px 40px 40px -20px rgba(13, 48, 189, 0.151826);
}